<template>
  <div class="manage-board">
    <ManageHeader></ManageHeader>
    <router-view></router-view>
    <div class="right-slide-tip-container">
      <transition v-for="ele in warningTips"
                  :key="ele + new Date().getMilliseconds()"
                  name="slide"
                  out-in
                  appear
      >
        <slide-tip :warningTip="ele.tip" :tipType="ele.type"></slide-tip>
      </transition>
    </div>
  </div>
</template>

<script>

import ManageHeader from "@/pages/index/components/ManageHeader";
import {Timer} from "@/assets/js/utilsClass";
import axios from "axios";
import Constants from "@/assets/js/consts";

export default {
  name: "manageIndex",
  data() {
    return {
      warningTips: [],
    }
  },
  components: {
    ManageHeader,
  },
  mounted() {
    this.$bus.$on('addWarningTip', this.addWarningTip);
    this.$bus.$on('sendRequest', this.sendRequest);
  },
  methods: {
    addWarningTip(tip, type) {
      let timer = new Timer();
      let time = tip.length / 10.0 * 2000;  // 延迟时间随字符串长度改变

      this.warningTips.push({
        tip,
        type
      });
      time = time > 2000 ? time : 2000;
      // 延迟删除
      timer.timeout(() => {
        this.warningTips.splice(this.warningTips.indexOf(tip), 1);
        timer.destroy();  // 定时器删除
      }, time);
    },
    // 发起请求, axios
    sendRequest(servlet, fn, formData) {
      axios({
        url: Constants.URL + servlet,
        method: 'post',
        data: formData
      }).then(fn);
    },
  },
  beforeDestroy() {
    this.$bus.$off('addWarningTip');
    this.$bus.$off('sendRequest');
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/*整个面板*/
.manage-board {
  width: 100%;
  height: 1000px;
}

.right-slide-tip-container {
  position: fixed;
  top: 0px;
  right: 0px;
  z-index: 9999999;
  margin: 20px 30px 0 0;
}

.right-slide-tip-container {
  display: flex;
  flex-direction: column;
}

/*起点*/
.slide-enter {
  transform: translateX(100%);
  opacity: 0;
}

/*终点*/
.slide-enter-to {
  transform: translateX(0);
  opacity: 1;
}

.slide-leave-to {
  opacity: 0;
}

.fade-enter, .fade-leave-to {
  opacity: 0;
}

.fade-enter-to, .fade-leave {
  opacity: 1;
}
</style>